.HiddenSettings {
  position: absolute;
  top: 44px;
  left: 0;
  width: 0px;
  min-height: 100%;
  background-color:rgba(0, 0, 0, 0.5);

  vertical-align: top;
  display: inline-block;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.Settings {
  position: absolute;
  top: 44px;
  left: 0;
  width: 100%;
  min-height: 100%;
  background-color:rgba(0, 0, 0, 0.5);

  vertical-align: top;
  display: inline-block;
  padding: 10px;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.SettingsPanel {
  background-color:rgba(255, 255, 255, 0.7);
  vertical-align: top;
  display: inline-block;
  padding: 10px;
}

.SettingsGroup {
  margin-top: 10px;
  margin-bottom: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  max-width: 600px;
}

.PlayersSettings {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: left;
      justify-content: left;
  width: 600px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.playerSettings {
  display: block;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid black;
  display: inline-block;
  -ms-flex-pack: left;
      justify-content: left;
  max-width: 200px;
}




